@font-face {
  font-family: "FF Mark regular";
  src: url("/static/font/ff-mark/ff-mark-regular.ttf") format("truetype");
}
@font-face {
  font-family: "FF Mark light";
  src: url("/static/font/ff-mark/ff-mark-light.ttf") format("truetype");
}
.chartContainer {
  width: 90%;
  max-width: 1600px;
  padding: 0;
  margin-bottom: 100px;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
}
@media (min-width: 1800px) {
.chartContainer {
      width: 100%;
}
}
.chartContainer svg {
    position: relative;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
}
.chartContainer .axis-x text {
    font-size: 12px;
    font-weight: 100;
    font-family: 'FF Mark light', sans-serif;
    fill: #ecf0f1;
}
.chartContainer .axis-x line,
  .chartContainer .axis-x path {
    display: none;
    stroke: #ecf0f1;
}
.chartContainer .axis-area text {
    fill: #FF5A4F;
}
.chartContainer .axis-area path,
  .chartContainer .axis-area line {
    stroke: #FF5A4F;
    stroke-width: 2;
}
.chartContainer .grid-area .domain,
  .chartContainer .grid-area text {
    display: none;
}
.chartContainer .grid-area .tick line {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
    stroke: rgba(255, 255, 255, 0.25);
    stroke-dasharray: 5 5;
}
.chartContainer .axis-grow text {
    fill: #bf4600;
}
.chartContainer .axis-grow path,
  .chartContainer .axis-grow line {
    stroke: #ff9659;
    stroke-width: 2;
}
.chartContainer path.areaLine {
    fill: none;
    stroke: #ff4f61;
    stroke-width: 1px;
}
.chartContainer path.area {
    fill: none;
    stroke: none;
    fill: url(#areaGradient);
}
.chartContainer .area-stop-0 {
    stop-color: rgba(255, 90, 79, 0.4);
}
.chartContainer .area-stop-1 {
    stop-color: rgba(255, 90, 79, 0.1);
}
.chartContainer path.growLine {
    fill: none;
    stroke: #ff9a26;
    stroke-width: 1px;
}
.chartContainer path.grow {
    fill: none;
    stroke: none;
    fill: url(#growGradient);
}
.chartContainer .grow-stop-0 {
    stop-color: rgba(255, 118, 38, 0.4);
}
.chartContainer .grow-stop-1 {
    stop-color: rgba(255, 118, 38, 0.1);
}
.chartContainer .pointGrow {
    fill: #ffbe26;
    stroke: #ff9a26;
}
.chartContainer .pointArea {
    fill: #ff954f;
    stroke: #ff774f;
}
.chartContainer .pointBase {
    fill: #05101e;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
}
.chartContainer .tooltip {
    opacity: 0;
    transition: opacity 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.chartContainer .tooltip.visible {
    opacity: 1;
}
.chartContainer .tooltip line {
    stroke: rgba(255, 255, 255, 0.25);
    stroke-width: 2;
    stroke-dasharray: 5;
}
.chartContainer .tooltip-html {
    opacity: 0;
    position: absolute;
    width: 120px;
    top: 200px;
    left: 200px;
    border: 1px solid #FF5A4F;
    border-radius: 10px;
    background-color: rgba(5, 16, 30, 0.5);
    font-size: 16px;
    padding: 10px;
    transition: opacity 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
    pointer-events: none;
}
.chartContainer .tooltip-html p {
      line-height: 24px;
      margin: 0;
}
.chartContainer .tooltip-html p.population:before,
    .chartContainer .tooltip-html p.area:before {
      display: inline-block;
      content: ' ';
      background-size: 24px 24px;
      height: 24px;
      width: 24px;
      margin-right: 10px;
      vertical-align: middle;
}
.chartContainer .tooltip-html p.population:before {
      background-image: url("/static/icon_people.svg");
}
.chartContainer .tooltip-html p.area:before {
      background-image: url("/static/icon_space.svg");
}
.chartContainer .tooltip-html.visible {
    opacity: 1;
}



