@font-face {
  font-family: "FF Mark regular";
  src: url("/static/font/ff-mark/ff-mark-regular.ttf") format("truetype");
}
@font-face {
  font-family: "FF Mark light";
  src: url("/static/font/ff-mark/ff-mark-light.ttf") format("truetype");
}

/* global styles */
html,
body {
  height: 100vh;
  overflow: hidden;
  margin: 0;
}
#app {
  font-family: 'FF Mark light', sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  height: 100vh;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #05101e linear-gradient(15deg, #05101e, #112E3E);
}
div.dg.ac {
  z-index: 1000;
}



